.Portfolio_gitIcons__26lo4 {
    font-size: 16px;
    font-weight: 700;
    margin: 0px;
    padding: 6px;
    text-align: center;
    font-family: 'Open Sans', sans-serif;    
} 

.Portfolio_particleContainer__3W2H6{
    position: relative;
    top: 0;
    left: 0;
    max-height: 100%;
}

.Portfolio_portfolioWrapper__1ymBS{
    position: relative;
    width: 100vw;
    box-sizing: border-box;
}

.Portfolio_projectGridWrapper__3y74S{
    padding: 20px;
    /* position: absolute; */
    top: 0px;
    left: 0;
    height: 100%;
    width: 100vw;
    box-sizing: border-box;

}

.Portfolio_projectImg__2fCFz{
    width: 100%;
    border-radius: 10px;
    border: 1px solid #220022;
}

.Portfolio_projectImgWrapper__1Ppl-{
    grid-column: 2/3;
    grid-row: 1/2;
}

.Portfolio_projectImgWrapperAlt__1CWir{
    grid-column: 1/2;
    grid-row: 1/2;
}

.Portfolio_projectRow__2HKw2{
    width: 80%;
    height: 320px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 50% auto;
    grid-row: 100%;
    grid-gap: 40px;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    padding: 20px;
}

.Portfolio_projectText__1bXf5{
    grid-column: 1/2;
    grid-row: 1/2;
    margin-top: 10px;
}

.Portfolio_projectTextAlt__2aFAw{
    grid-column: 2/3;
    grid-row: 1/2;
    margin-top: 10px;    
}

.Portfolio_projectRow__2HKw2>a>h2,
.Portfolio_projectText__1bXf5>a>h2, 
.Portfolio_projectTextAlt__2aFAw>a>h2{
    font-size: 2rem;
    text-align: center;
    font-family: 'Lato', sans-serif;
    font-weight: 800;
}

.Portfolio_projectRow__2HKw2>a>i,
.Portfolio_projectText__1bXf5>a>i,
.Portfolio_projectTextAlt__2aFAw>a>i{
    padding-right: 10px;
}

.Portfolio_projectRow__2HKw2 p,
.Portfolio_projectText__1bXf5 p,
.Portfolio_projectTextAlt__2aFAw p{
    text-align: justify;
    font-size: 14px;
    margin-bottom: 0.1rem;
    font-family: 'Open Sans', sans-serif;
}



/* Media Queries */
/* ============= */
 
/* Very Small Screens - Smaller than 576px */
@media (max-width: 575.98px) { 

    .Portfolio_projectGridWrapper__3y74S{        
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
    }

    .Portfolio_projectImg__2fCFz{
        width: 100%;
    }

    .Portfolio_projectRow__2HKw2{
        display: block;
        width: 95%;
        height: auto;
        padding: 5px;
    }

    .Portfolio_projectText__1bXf5, 
    .Portfolio_projectTextAlt__2aFAw, 
    .Portfolio_projectImgWrapper__1Ppl-,
    .Portfolio_projectImgWrapperAlt__1CWir{
        display: block;
        margin: 10px auto;
        width: 100%;
    }

    .Portfolio_projectText__1bXf5>a>h2{
        font-size: 1.5rem;
    }
    
}


/* Small Devices - (landscape Phones - 576px to 768) */
@media (min-width: 576px) and (max-width: 767.98px) {

    .Portfolio_projectRow__2HKw2{
        display: block;
        width: 84%;
        height: auto;
        padding: 5px;
        margin-bottom: 50px;
    }
}

/* Medium devices (Tablets - 768px and Up) */
@media (min-width: 768px) and (max-width: 991.98px) { 

 
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) { 
  
    .Portfolio_projectRow__2HKw2{
        height: 230px;
    }


}

